.dy-httpupload-left {
    float: left;
}

.dy-httpupload-right {
    float: right;
}

.dy-httpupload-style1 {
    border: 1px #eee solid;
    width: 640px;
    height: 400px;
    box-shadow: 0px 0px 10px #ddd;
    border-radius: 7px 7px 0px 0px;
    position: fixed;
    right: 30px;
    bottom: -5px;
    z-index: 99999;
    font-size: 12px;
    font-family: "΢���ź�";
    background: #FFF;
    display: none;
}

.dy-httpupload-style1-top {
    height: 45px;
    line-height: 45px;
    width: 96%;
    padding: 0px 2%;
    border-top-left-radius: 7px;
    border-top-right-radius: 7px;
    background-color: #fff;
    border-bottom: 1px #f4f4f4 solid;
}

.dy-httpupload-style1-content {
    width: 100%;
    margin: 0px auto;
    position: relative
}

.dy-httpupload-style1-content-table {
    width: 100%
}

.dy-httpupload-style1-content-table th {
    padding: 12px 8px;
    border-bottom: 1px #f4f4f4 solid;
    text-align: left;
}

.dy-httpupload-style1-content-table td {
    padding: 8px;
    border-bottom: 1px #f4f4f4 solid;
    text-align: left;
    color: #333
}

.dy-httpupload-style2 {
    border-bottom: 1px #f4f4f4 solid;
    width: 620px;
    padding: 0px 10px;
    height: 45px;
    line-height: 45px;
}

.dy-httpupload-style2-2 {
    float: left;
    width: 55%;
    padding: 0px 5px;
}

.dy-httpupload-style2-3 {
    float: left;
    width: 20%;
    padding: 0px 5px;
}

.dy-httpupload-style2-5 {
    float: left;
    width: 10%;
    padding: 0px 5px;
}

.dy-httpupload-style2-6 {
    float: left;
    width: 8%;
    padding: 0px 5px;
}

.dy-httpupload-style2-2-3 {
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    padding-left: 30px
}

.dy-httpupload-style4 {
    color: #158A38;
    font-size: 18px;
    margin-top: 14px;
}

.dy-httpupload-style5 {
    color: #999;
    font-size: 18px;
    margin-top: 14px;
    cursor: pointer;
}

.dy-httpupload-jindu {
    width: 0%;
    background: #e2eeff;
    position: relative;
}

.dy-httpupload-style2-5 a {
    text-decoration: none;
    color: #F90006;
    margin-right: 10px;
    cursor: pointer
}

.dy-httpupload-style2-4 a {
    text-decoration: none;
    color: #0F8BF0;
}

.dy-httpupload-style1{
	width: 100%;
	right: 0;
	height: 150px;
}

.dy-httpupload-style2{
	width: 100%;
}

.dy-httpupload-style2-2{
	padding: 0;
}
.dy-httpupload-style2-3{
	padding: 0;
}
.dy-httpupload-style2-5{
	padding: 0;
}
.dy-httpupload-style2-6{
	padding: 0;
}



