
.return {
	vertical-align: middle;
	margin-right: 10px;
}
/*demo*/
.flex {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: -webkit-box;
	-webkit-box-pack: start;
	-webkit-box-align: start;
	display: -webkit-flex;
	-webkit-justify-content: space-between;
	-webkit-align-items: top;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: justify;
	    -ms-flex-pack: justify;
	        justify-content: space-between;
	-webkit-box-align: top;
	    -ms-flex-align: top;
	        align-items: top;
	-webkit-box-orient: horizontal;
	-webkit-box-direction: normal;
	    -ms-flex-flow: row wrap;
	        flex-flow: row wrap
}
.flex>div {
	margin: 10px;
	padding: 20px;
	width: 25%;
	min-width: 300px;
	border: 1px solid #eee;
	border-radius: 2px;
	position: relative;
}
.flex>div>span {
	position: absolute;
	left: 0px;
	top: 0px;
	padding: 2px 10px;
	font-size: 10px;
	border-radius: 0 0 2px 0;
	background: #ea6151;
	color: #fff;
}
.flex>div>input {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width: 100%;
	margin-top: 20px;
	border-radius: 2px;
	border: 1px solid #dedede;
	padding: 10px;
	font-size: 16px;
	background: url(data:image/svg+xml;base64,) no-repeat 8px 10px;
	padding-left: 36px;
	color: #666;
}
/*transition*/
.fade-enter-active,
.fade-leave-active {
	-webkit-transition: all .5s ease-in-out;
	transition: all .5s ease-in-out;
}
.fade-enter,
.fade-leave-active {
	opacity: 0;
	-webkit-transform: translateY(-10px);
	        transform: translateY(-10px);
}
/*下拉框*/
.calendar-dropdown {
	background: #fff;
	position: absolute;
	left: 0;
	top: 0;
	padding: 20px;
	border: 1px solid #eee;
	border-radius: 2px;
}
.calendar-dropdown:before {
	position: absolute;
	left: 30px;
	top: -10px;
	content: "";
	border: 5px solid rgba(0, 0, 0, 0);
	border-bottom-color: #DEDEDE;
}
.calendar-dropdown:after {
	position: absolute;
	left: 30px;
	top: -9px;
	content: "";
	border: 5px solid rgba(0, 0, 0, 0);
	border-bottom-color: #fff;
}
/*弹出框*/
.calendar-dialog {
	position: absolute;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
}
.calendar-dialog-mask {
	background: rgba(255, 255, 255, .5);
	width: 100%;
	height: 100%;
}
.calendar-dialog-body {
	background: #fff;
	position: absolute;
	left: 50%;
	top: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	padding: 20px;
	border: 1px solid #eee;
	border-radius: 2px;
}
.dateSelect {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	width: 100%;
	left: 0;
	bottom: 0;
	z-index: 9999;
}
.dateSelect button {
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	border-radius: 0;
	font-size: 14px;
}
.flex {
	padding-bottom: 40px;
}



